@import url(../assets/fonts/Alegreya/alegreya-regular.css);
@import url("https://fonts.googleapis.com/css?family=Didact+Gothic&display=swap");
body {
  height: 100vh; }

#root {
  height: 100%; }

#application-layout {
  display: grid;
  grid-template-areas: "header" "content" "footer";
  grid-auto-rows: 13% 1fr 50px;
  align-items: center;
  height: 100%; }
  #application-layout header.app-header {
    grid-area: header;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center; }
  #application-layout article.app-content {
    grid-area: content;
    justify-content: start;
    height: 100%; }
  #application-layout footer.app-footer {
    height: 100%;
    grid-area: footer;
    font-family: "Didact Gothic", sans-serif;
    display: grid;
    background-color: #48559d;
    color: #fff;
    justify-items: center;
    align-items: center; }

/* Custom radio buttons*/
[type="radio"]:checked,
[type="radio"]:not(:checked) {
  position: absolute;
  left: -9999px; }

[type="radio"]:checked + label,
[type="radio"]:not(:checked) + label {
  position: relative;
  padding-left: 28px;
  cursor: pointer;
  line-height: 20px;
  display: inline-block;
  color: #000; }

[type="radio"]:checked + label:before,
[type="radio"]:not(:checked) + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 18px;
  height: 18px;
  border: 1px solid #48559d;
  border-radius: 100%;
  background: #fff; }

[type="radio"]:checked + label:after,
[type="radio"]:not(:checked) + label:after {
  content: '';
  width: 12px;
  height: 12px;
  background: #48559d;
  position: absolute;
  top: 4px;
  left: 4px;
  border-radius: 100%;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease; }

[type="radio"]:not(:checked) + label:after {
  opacity: 0;
  -webkit-transform: scale(0);
  transform: scale(0); }

[type="radio"]:checked + label:after {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1); }
